.tabs {
    margin-top: 10px;
    display: flex;
    flex-direction: column;
    width: 100%;
  }
  
  .tab-buttons {
    display: flex;
    overflow-x: auto;
    white-space: nowrap;
    background: #2e3191;
  }
  
  .tab-buttons button {
    background: #2e3191;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.3s;
    color: #ffff;
    min-width: 100px;
    flex: 0 0 auto;
  }
  
  .tab-buttons button.active {
    font-weight: bold;
    background-color: #f0f0f0;
     color: var(--bs-primary2);
  }
  
  .tab-buttons button:hover {
    background-color: #f0f0f0;
  }
  
  .tab-content {
    width: 100%;
    border-top: none;
  }
  